import React, { Component } from "react";

let Avatar = (props) => {
  return (
    <>
      <div className="col-6">
        <img
          className="img img-thumbnail"
          src={props.src}
          width="200px"
          alt="Profile Pic"
        />
      </div>
      <div className="col-12">
        <h1>Anthony</h1>
      </div>
    </>
  );
};

